<template>
  <wrapper titulo="Mis ordenes">
    <v-row v-if="ordenes.length > 0">
      <v-col v-for="(orden, indexOrden) in ordenes" :key="indexOrden" cols="12" class="mb-4">
        <v-card class="pa-2">
          <v-card-title>
            Orden #{{ orden.id }}
            <small v-if="orden.estatus" class="primary--text">&nbsp;{{ orden.estatus.titulo }}</small>
          </v-card-title>
          <v-card-title class="success--text font-weight-bold pt-0 pb-0">{{orden.costo |moneda}}</v-card-title>
          <v-card-title class="py-0 font-weight-bold grey--text my-0">
            {{ orden.alumno.nombreCompleto }}
          </v-card-title>
          <v-card-title class="pt-0 font-weight-bold caption">
            {{ orden.fecha | fecha }}
          </v-card-title>

          <v-card-text>
            <v-row>
              <v-col cols="12">
                <h2 v-if="orden.productos[0].menu" class="font-weight-bold ">Pertenece al Menú "{{orden.productos[0].menu.nombre}}"</h2>
              </v-col>
              <v-col cols="12" sm="6" v-for="(producto, indexProducto) in orden.productos" :key="indexProducto" class="mb-4">
                <producto-en-orden :producto="producto" :opcion-quitar="!producto.menu && !orden.habilitarEditarOrden"
                                   @cambiar-cantidad="cambiarCantidad($event, orden, producto)"
                                   @quitar="abrirDialogQuitarProducto(indexProducto, orden)" :cantidad-editable="orden.habilitarEditarOrden"/>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions v-if="orden.estatus && orden.estatus.titulo === 'CREADA'"
                          class="text-center" style="display: flex;justify-content: center; align-items: center;">




            <template v-if="!orden.habilitarEditarOrden">
              <v-btn rounded color="primary" @click="$set(orden, 'habilitarEditarOrden', true)">Editar cantidades</v-btn>
              <v-btn  rounded color="primary"
                      @click="abrirDialogAgregarProducto(orden)">Agregar más productos</v-btn>
              <v-btn rounded color="error"  @click="abrirDialogCancelarOrden({orden, indexOrden})">Cancelar</v-btn>
            </template>

            <template v-else>
              <v-btn rounded color="success" @click="actualizarOrden(orden)">Guardar cantidades</v-btn>
              <v-btn rounded color="primary" @click="$set(orden, 'habilitarEditarOrden', false)">Cancelar</v-btn>
            </template>


          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/ordenes_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">Aún no tiene ordenes creadas</h2>
      </v-col>
      <v-col cols="12" md="6" class="text-center">
        <v-btn rounded :to="{ name: 'DashboardMenu' }"><span style="font-family: Nunito;" class="primary--text">Ir a menús</span></v-btn>
      </v-col>
    </v-row>
    <dialogo v-model="dialogoAgrgarProductos" title="Agregar Productos a la orden" ancho="700">
      <v-row v-if="ordenParaActualizar.alumno">
        <v-col cols="12">
          <h4 class="caption">Seleccione los productos que desea agregar a su orden</h4>
        </v-col>
          <v-col cols="12">
            <carrusel>
              <carrusel-item v-for="(categoria, indexCategorias) in categorias" :key="indexCategorias" size="41%">
                <categoria :categoria="categoria" :esta-seleccionada="categoriaSeleccionada && categoria.id === categoriaSeleccionada" @seleccionarCategoria="seleccionarCategoria(categoria)"/>
              </carrusel-item>
            </carrusel>
          </v-col>
        <v-col v-for="(producto, indexProducto) in productosFiltrados" :key="indexProducto" cols="12" md="6">
          <producto :producto="producto" opcion-agregar mostrar-acciones
                    :producto-agregado="existeProductoEnOrdenParaActualizar(producto)" @seleccionar="agregarProductoOrden(producto)"
                    :productos-bloqueados-alumno="ordenParaActualizar.alumno.productosBloqueados" />
        </v-col>
      </v-row>
    </dialogo>
    <dialogo v-model="dialogDeseaCancelarOrden" title="¿Desea cancelar la orden?">
      <v-row v-if="ordenACancelar.orden">
        <v-col cols="12">
          <h2>Se cancelará la orden creada para el alumno <span class="text--disabled">{{ordenACancelar.orden.alumno.nombreCompleto}}</span>
            para el día {{ordenACancelar.orden.fecha}}
          </h2>
        </v-col>
      </v-row>
      <template slot="actions">
        <v-btn text color="primary" plain @click="cancelarOrden">Aceptar</v-btn>
      </template>
    </dialogo>
    <dialogo v-model="dialogQuitarProducto" title="¿Desea quitar el producto?">
      <template slot="actions">
        <v-btn text color="primary" plain @click="quitarProducto">Aceptar</v-btn>
      </template>
    </dialogo>
    <v-snackbar v-model="mensajeProductoAgregado" color="success" bottom app>Producto agregado</v-snackbar>
    <v-snackbar v-model="mensajeProductoEliminado" color="success" bottom app>Producto eliminado</v-snackbar>
  </wrapper>
</template>

<script>
    import Carrusel from "../General/Carrusel/Carrusel";
    import CarruselItem from "../General/Carrusel/CarruselItem";
    import ProductosService from "../../services/ProductosService";
    import CategoriasService from "../../services/CategoriasService";
    import Categoria from "../Tienda/Categoria";
    import MenusService from "../../services/MenusService";
    import OrdenesService from "../../services/OrdenesService";
    import ProductoEnOrden from "./ProductoEnOrden";
    import Producto from "../Tienda/Producto";
    import CafeteriasService from "../../services/CafeteriasService";
    import SaldoService from "../../services/SaldoService";
    import AlumnosService from "../../services/AlumnosService";
    export default {
        name: "DashboardOrdenes",
      components: {Producto, ProductoEnOrden, Categoria, CarruselItem, Carrusel},
      data(){
          return {
            dialogoAgrgarProductos: false,
            categorias: [],
            productos: [],
            menus: [],
            ordenes: [],
            productosFiltrados:[],
            categoriaSeleccionada: null,
            ordenParaActualizar: {},
            mensajeProductoAgregado: false,
            mensajeProductoEliminado: false,
            dialogDeseaCancelarOrden: false,
            ordenACancelar: {},
            productoParaQuitar: {},
            dialogQuitarProducto: false
          }
      },
      async created() {
        this.$loader = true
        try {
          const getOrdenesPorEstatus = OrdenesService.getPromiseOrdenesByIdAndEstatus(this.$usuario)
          this.ordenes = await getOrdenesPorEstatus(this.$usuario.id, 'CREADA')
          ordenarOrdenes(this.ordenes)
        } catch (e) {
            console.log(e)
        }
        try {
          this.categorias = await CategoriasService.getCategorias();
        } catch (e) {
          this.categorias = []
          console.log(e)
        }
        try {
          this.productos = await ProductosService.getProductos();
        } catch (e) {
          this.productos = []
          console.log(e)
        }
        if(!this.categoriaSeleccionada){
          this.productosFiltrados = this.productos
        }
        this.$loader = false

        const vm = this
        if (this.$usuario.esPadreDeFamilia) {
          for (const hijo of this.$usuario.hijos) {
            CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, hijo.alumno.id)
              .then(cafeterias => {
                vm.$set(hijo.alumno.cafeteria, 'saldoAlumno',
                  vm.$getSaldoDeAlumnoParaCafeteria(hijo.alumno.cafeteria.id, cafeterias))
              })
              .catch(() => {})
          }
        } else if (this.$usuario.esAlumno) {
          CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, vm.$usuario.id)
            .then(cafeterias => {
              vm.$set(vm.$usuario.cafeteria, 'saldoAlumno',
                vm.$getSaldoDeAlumnoParaCafeteria(vm.$usuario.cafeteria.id, cafeterias))
            })
            .catch(() => {})
        }
      },
      methods: {
        seleccionarCategoria(categoria) {
          let vm = this
          vm.categoriaSeleccionada = categoria.id
          if (vm.categoriaSeleccionada){
            vm.productosFiltrados = vm.productos.filter(function (productoFiltrado) {
              let idCategoriaProducto = productoFiltrado.categoria.id;
              return (idCategoriaProducto == vm.categoriaSeleccionada);
            })
          }else {
            vm.productosFiltrados = vm.productos
          }
        },
        abrirDialogAgregarProducto(orden){
          this.ordenParaActualizar = orden
          this.dialogoAgrgarProductos = true
        },
        quitarProducto(){
          this.$delete(this.productoParaQuitar.orden.productos, this.productoParaQuitar.indexProducto)
          actualizarSubtotalOrden(this, this.productoParaQuitar.orden)

          const vm = this
          vm.$loader = true
          OrdenesService.updateOrdenes(this.productoParaQuitar.orden)
            .then(orden => {
              vm.mensajeProductoEliminado = true
            })
            .catch(error => {
              vm.$router.go();
            })
            .finally(() => {
              vm.dialogQuitarProducto = false
              vm.$loader = false
            })
        },
        agregarProductoOrden(producto){
          const alumno = getAlumnoDeUsuario(this, this.$usuario, this.ordenParaActualizar.alumno)
          if (SaldoService.esSaldoSuficiente(this.ordenes, alumno, producto, 1)) {
            this.ordenParaActualizar.productos.push({ id: 0, cantidad: 1, costo: producto.costo, producto })
            actualizarSubtotalOrden(this, this.ordenParaActualizar)

            const vm = this
            vm.$loader = true
            OrdenesService.updateOrdenes(this.ordenParaActualizar)
              .then(orden => {
                vm.$set(this.ordenParaActualizar, 'productos', orden.productos)
                vm.mensajeProductoAgregado = true
              })
              .catch(error => {
                vm.$showAlerta('600', 'warning', 'mdi-alert-circle',
                  'No fue posible agregar el producto', false);
                vm.$router.go();
              })
              .finally(() => {
                vm.$loader = false
              })
          } else {
            this.$showAlerta('600', 'warning', 'mdi-alert-circle',
              'El saldo no es suficiente.', false);
          }
        },
        actualizarOrden(orden){
          const vm = this
          vm.$loader = true
          OrdenesService.updateOrdenes(orden)
            .then(orden => {
              vm.$showAlerta('600', 'success', 'mdi-check-circle-outline',
                'Orden actualizada.', false);
            })
            .catch(error => {
              vm.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'No fue posible actualizar la orden.', false);
              vm.$router.go();
            })
            .finally(() => {
              vm.$loader = false
              vm.$set(orden, 'habilitarEditarOrden', false)
            })
        },
        abrirDialogCancelarOrden(ordenParaCancelar){
          this.ordenACancelar = ordenParaCancelar
          this.dialogDeseaCancelarOrden = true
        },
        async cancelarOrden (){
          this.$loader = true
          try {
            let ordernCancelada = await OrdenesService.cancelarOrden(this.ordenACancelar.orden.id)
            this.$set(this.ordenes, this.ordenACancelar.indexOrden, ordernCancelada)
          }catch (e) {
            this.$showAlerta('600', 'warning', 'mdi-alert-circle',
              'No fue posible cancelar la orden', false);
          }
          this.$loader = false
          this.dialogDeseaCancelarOrden = false
        },
        existeProductoEnOrdenParaActualizar(producto) {
          return this.ordenParaActualizar.productos &&
            this.ordenParaActualizar.productos.some(p => p.producto.id === producto.id)
        },
        cambiarCantidad(accion, orden, producto) {
          if (accion === 'agregar') {
            const alumno = getAlumnoDeUsuario(this, this.$usuario, orden.alumno)
            if (SaldoService.esSaldoSuficiente(this.ordenes, alumno, producto.producto, producto.cantidad + 1))
              this.$set(producto, 'cantidad', producto.cantidad + 1)
            else
              this.$showAlerta('600', 'warning', 'mdi-alert-circle', 'El saldo no es suficiente.', false);
          } else if (accion === 'restar') {
            this.$set(producto, 'cantidad', producto.cantidad - 1)
          }
          actualizarSubtotalOrden(this, orden, producto)
        },
        abrirDialogQuitarProducto(indexProducto, orden){
          this.productoParaQuitar.indexProducto = indexProducto
          this.productoParaQuitar.orden = orden
          this.dialogQuitarProducto = true
        }
      }
    }

    function ordenarOrdenes(ordenes) {
      ordenes.sort((a, b) => b.fecha.localeCompare(a.fecha))
    }

    function actualizarSubtotalOrden(vm, orden, producto) {
      if (producto)
        vm.$set(producto, 'costo', producto.cantidad * producto.producto.costo)

      vm.$set(orden, 'costo', orden.productos.reduce((total, p) => total + p.costo, 0))
      // Espacio para condición para validar si el saldo del alumno es suficiente
    }

    function getAlumnoDeUsuario(vm, usuario, alumno) {
      if (usuario.esPadreDeFamilia) {
        const alumnos = usuario.hijos.map(function (hijo) {
          return hijo.alumno
        });
        // Devuelve alumno hijo del usuario, el cual tiene saldo
        const indexHijo = vm.$getIndex(alumnos, 'id', alumno)
        const hijo = usuario.hijos[indexHijo]
        return hijo.alumno
      } else if (usuario.esAlumno)
        return usuario;
      else
        throw new Error("User type not found");
    }
</script>

<style scoped>

</style>
