import AuthConst from '../config/AuthConst';
import http from "./http";

const getAlumnos = () => {
  //se debe quitar cuando se configure clienteInternet
  let url = AuthConst.BASE_URL + '/alumnos/cliente/1';
  return http.get(url)
};

const updateAlumno = (alumno) => {
  let url = AuthConst.BASE_URL + '/alumnos';
  return http.put(url, alumno)
};

const busquedaPorNombre = (nombre, apellidoPaterno, apellidoMaterno) => {
  let url = AuthConst.BASE_URL + '/alumnos/nombre/' + nombre + '/apellido-paterno/' + apellidoPaterno + '/apellido-materno/' + apellidoMaterno;
  return http.get(url)
};


const busquedaPorMatricula = (matricula) => {
  let url = AuthConst.BASE_URL + '/alumnos/matricula/' + matricula;
  return http.get(url)
};

const desasignarAlumno = (idHijo) => {
  let url = AuthConst.BASE_URL + '/clientes-internet/hijos/' + idHijo;
  return http.remove(url)
};

export default { getAlumnos, updateAlumno, busquedaPorNombre, busquedaPorMatricula, desasignarAlumno }
