import AuthConst from '../config/AuthConst';
import http from "./http";

const getOrdenesPorCliente = (idCliente) => {
  let url = AuthConst.BASE_URL + '/ordenes/id-cliente-internet/' + idCliente + '/hijos';
  return http.get(url)
};

const createOrdenesBatch = (ordenes) => {
  let url = AuthConst.BASE_URL + '/ordenes/batch';
  return http.post(url, ordenes)
};

const cancelarOrden = (idOrden) => {
  let url = AuthConst.BASE_URL + '/ordenes/'+ idOrden +'/cancelar';
  return http.patch(url)
};

const updateOrdenes = (orden) => {
  let url = AuthConst.BASE_URL + '/ordenes';
  return http.put(url, orden)
};

const getOrdenesPorClienteAndEstatus = (idCliente, estatus) => {
  let url = AuthConst.BASE_URL + '/ordenes/id-cliente-internet/' + idCliente + '/hijos/estatus/' + estatus;
  return http.get(url)
}

const getPorIdAlumno = (idAlumno) => {
  let url = AuthConst.BASE_URL + '/ordenes/id-alumno/' + idAlumno
  return http.get(url)
};

const getPorMatriculaAlumno = (matricula) => {
  let url = AuthConst.BASE_URL + '/ordenes/matricula-alumno/' + matricula
  return http.get(url)
};

const getPorIdAlumnoAndEstatus = (idAlumno, estatus) => {
  let url = AuthConst.BASE_URL + '/ordenes/id-alumno/' + idAlumno + '/estatus/' + estatus
  return http.get(url)
};

const getPromiseOrdenesById = (usuario) => {
  if (usuario.esPadreDeFamilia)
    return getOrdenesPorCliente
  else if (usuario.esAlumno)
    return getPorIdAlumno
  else
    throw new Error("User type not found")
};

const getPromiseOrdenesByIdAndEstatus = (usuario) => {
  if (usuario.esPadreDeFamilia)
    return getOrdenesPorClienteAndEstatus
  else if (usuario.esAlumno)
    return getPorIdAlumnoAndEstatus
  else
    throw new Error("User type not found")
};

const entregarOrden = (idOrden) => {
  let url = AuthConst.BASE_URL + '/ordenes/'+ idOrden +'/entregar';
  return http.patch(url)
};

export default { getOrdenesPorCliente, createOrdenesBatch, updateOrdenes, cancelarOrden, getOrdenesPorClienteAndEstatus,
  getPorIdAlumno, getPorMatriculaAlumno, getPorIdAlumnoAndEstatus, getPromiseOrdenesById, getPromiseOrdenesByIdAndEstatus, entregarOrden }
