import AuthConst from '../config/AuthConst';
import http from "./http";

const getByCliente = (idCliente) => {
  let url = AuthConst.BASE_URL + '/menus/id-cliente/' + idCliente;
  return http.get(url)
};

const getMenusDisponibles = () => {
  let url = AuthConst.BASE_URL + '/menus/disponibles';
  return http.get(url)
};

const getMenusSemanales = () => {
  let url = AuthConst.BASE_URL + '/menus/semanales';
  return http.get(url)
};

const crearMenu = (menu) => {
  let url = AuthConst.BASE_URL + '/menus';
  return http.post(url, menu)
};

const updateMenu = (menu) => {
  let url = AuthConst.BASE_URL + '/menus';
  return http.put(url, menu)
}

export default { getByCliente, getMenusDisponibles, getMenusSemanales, crearMenu, updateMenu }
