<template>
  <v-card style="border-radius: 40px;" color="#eaf1f6" flat>
    <v-row>
      <v-col cols="4" class="text-center flex-column pr-0" style="display: flex">
        <div class="py-2 flex-column text-center alineacion">
          <v-img height="100px" width="100px" :src="imgSrc" lazy-src="../../assets/estado_vacio.svg" />
        </div>
      </v-col>
      <v-col cols="8" class="pl-0 ml-0">
        <v-card-text class="pa-0">
          <v-row>
            <v-col cols="12">
              <h3 style="font-family: Nunito;" class="black--text">{{ producto.producto.nombre }}</h3>
              <h4 class="text--disabled" style="font-family: Readex Pro, sans-serif;">{{ producto.producto.descripcion }}</h4>
              <h4 class="text--disabled" style="font-family: Readex Pro, sans-serif;">{{ producto.producto.contenidoNeto }}</h4>
              <h4 class="font-weight-bold precios--text" style="font-family: Readex Pro, sans-serif;">{{ producto.costo | moneda }}</h4>
              <h4 v-if="producto.menu && mostrarMensajeMenu"
                  class="caption font-weight-bold warning--text">Pertenece al menú {{producto.menu.nombre}}</h4>
            </v-col>
          </v-row>
        </v-card-text>
        <v-col cols="12" class="px-0">
          <div v-if="cantidadEditable">
            <v-btn :disabled="!!producto.menu || producto.cantidad <= 1" icon
                   small class="grey--text" @click="$emit('cambiar-cantidad', 'restar')">
              <v-icon>mdi-minus-box</v-icon>
            </v-btn>
            <span class="font-weight-bold"> {{producto.cantidad}} </span>
            <v-btn :disabled="!!producto.menu" icon small class="grey--text"
                   @click="$emit('cambiar-cantidad', 'agregar')">
              <v-icon>mdi-plus-box</v-icon>
            </v-btn>
            <h4 v-if="producto.menu" class="caption warning--text font-weight-bold">No se puede editar cantidad porque pertenece a un menú</h4>
          </div>
          <h4 v-else>{{ producto.cantidad }} pza(s)</h4>
        </v-col>
      </v-col>
    </v-row>
    <v-card-actions v-if="opcionQuitar" class="primary" style="display: flex;justify-content: center;align-items: center; background-color: #8bc34a">
      <v-btn text small dark @click="$emit('quitar')">Quitar</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
    export default {
        name: "ProductoEnOrden",
      props: {
        opcionQuitar: { type: Boolean, default: false },
        producto: {type: Object, required: true},
        cantidadEditable: {type: Boolean, default: false},
        mostrarMensajeMenu: {type: Boolean, default: false}
      },
      data(){
          return{
            verDetalles: false
          }
      },
        computed: {
            imgSrc (){
                return this.$getImgCategoria(this.producto.producto.categoria.id)
            }
        }
    }

</script>

<style scoped>

</style>
